import WxValidate from '../../../utils/WxValidate.js'
import uniPopup from "../../../components/uni-popup/uni-popup.vue"
let innerAudioContext = uni.createInnerAudioContext();
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			callData:{
				time: '67',
				type: '主动挂断，手机占线'
			},
			detailData:[
				{roleType:1,content:'喂，您好',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'需要什么时候还呐',audioStartTime:0,audioEndTime:2040,isClick: false},
				{roleType:1,content:'这个我也不太确定，我稍后给您回个电话。',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'你借款已逾期了，赶紧处理',audioStartTime:8030,audioEndTime:11230,isClick: false},
				{roleType:1,content:'您好，刚才没听清，请问你们这边是什么平台啊？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'哦，我这边是跨年贷的',audioStartTime:16520,audioEndTime:19190,isClick: false},
				{roleType:1,content:'这次欠款具体多少钱呢？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'现在本金5000',audioStartTime:23890,audioEndTime:26440,isClick: false},
				{roleType:1,content:'请问下最终还款日期是几号啊？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'昨天就已经到期了。',audioStartTime:32170,audioEndTime:34910,isClick: false},
				{roleType:1,content:'账单可以分期么？能否宽限几天呢？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'今天已经逾期了。',audioStartTime:34910,audioEndTime:36930,isClick: false},
				{roleType:1,content:'好的，那这边请问有别的事情吗？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:2,content:'不能',audioStartTime:38030,audioEndTime:39740,isClick: false},
				{roleType:1,content:'喂。在吗？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:1,content:'喂，您能听到我的说话吗？',audioStartTime:0,audioEndTime:0,isClick: false},
				{roleType:1,content:'喂，在吗？我这边听不见您的声音，先挂了，再见。',audioStartTime:0,audioEndTime:0,isClick: false}
				],
				mobileCode: "获取验证码",
				time: 60,
				mobileTip: "",
				inputValue: "",
				smsTip: "",
				mobile: "",
				smsCode: "",
				isDisable: false,
				bindmobile: '',
				isCheck: false,
				checked: 'agree',
				recShow: false,
				playIcon: false,
				iconPlay: false,
				currentTime: '00:00', //当前播放时间
				durationTime: '00:00', //总时长
				current: 0, //slider当前进度
				loading: false, //是否处于读取状态
				paused: true, //是否处于暂停状态
				seek: false, //是否处于拖动状态
				duration: 100,
				itemStart: '',
				recordId: "",
				totalCur: 0,
				totalStart: 0,
				totalPlay: false,
				src: "",
				startTime: 0,
				itemSrc: "",
				blackShow: false
		}
	},
	onLoad() {
		this.initValidate();
	},
	methods: {
		// 点击按钮播放
		playItemRecord(start, end, index) {
			let _inn = uni.createInnerAudioContext();
			var j = 0
			for (let i = 0; i < this.detailData.length; i++) {
				if (index === i) {
					j = i
					this.detailData[index].isClick = !this.detailData[index].isClick
					this.$forceUpdate();
					if (this.detailData[index].isClick == true) {
						console.log(this.detailData[index].isClick)
						console.log('开始播放1');
						var that = this;
						_inn.src = "https://byrobot-prod.oss-cn-hangzhou.aliyuncs.com/InboundRobotCommunicate/20191231/20191231N3mAbc0000881013_user.wav"
						console.log('开始播放2');
						var flag = 0;
						_inn.play();
						_inn.onCanplay(function() {
							console.log('可以播放');
							if (flag == 1) {
								return;
							} else {
								_inn.seek(start / 1000);
								_inn.onSeeking(function() {
									// console.log('正在跳转');
								})
								_inn.onSeeked(function() {
									// console.log('完成跳转');
									flag = 1;
									var inerval = setInterval(() => {
										if (_inn.currentTime >= end / 1000) {
											_inn.stop()
											_inn.onStop()
											that.paused = true
											that.detailData[j].isClick = false
											that.$forceUpdate();
										}
									}, 100)
									_inn.onError((res) => {
										that.title = JSON.stringify(res);
									});
								})
							}
		
						});
						_inn.onError(function() {
							console.log('播放失败');
						})
		
					} else {
						console.log(this.detailData[index].isClick)
						_inn.pause()
						_inn.onPause(function(){
							console.log('暂停')
						})
					}
				}
		
			}
		},
		//播放音频
		playRecord() {
			this.playIcon = !this.playIcon
			var that = this
			this.totalPlay = !this.totalPlay
				that.recShow = true
				if (that.totalPlay) {
					this.current = 0
					var flag = 0
					var that = this
					innerAudioContext.autoplay = true //开始自动播放
					innerAudioContext.src = 'https://byrobot-prod.oss-cn-hangzhou.aliyuncs.com/InboundRobotCommunicate/20191231/20191231N3mAbc0000881013.wav'
					// 音频进度更新事件
					innerAudioContext.onTimeUpdate(() => {
						if (!this.seek) {
							this.current = innerAudioContext.currentTime
						}
					})
					// 音频播放
					innerAudioContext.onPlay(() => {
						this.duration = innerAudioContext.duration
						this.paused = false
						this.loading = false
					})
					innerAudioContext.onCanplay(function() {
						if (flag == 1) {
							return
						} else {
							innerAudioContext.seek(that.startTime / 1000)
							innerAudioContext.onSeeked(function() {
								flag = 1
							})
							innerAudioContext.onEnded(function() {
								console.log('自然播放结束')
								that.playIcon = false
							})
						}
					})
					// 音频暂停
					innerAudioContext.onPause(() => {
						this.paused = true
					})
					// 音频结束
					innerAudioContext.onEnded(() => {
						this.current = 0
						this.recShow = false
					})
					//音频完成更改进度事件
					innerAudioContext.onSeeked(() => {
						this.seek = false
					})
				} else {
					innerAudioContext.pause()
					innerAudioContext.onPause(function() {
						that.startTime = innerAudioContext.currentTime * 1000
						console.log('播放展厅')
					})
				}
		
		},
		userAgree(){
			uni.navigateTo({
				url:'../../../static/privacyAgreement/privacyAgreement'
			})
		},
		privacyAgree(){
			uni.navigateTo({
				url:'../../../static/userAgreement/userAgreement'
			})
		},
		back(){
			uni.navigateBack({
				
			})
		},
		// 规则校验
		initValidate() {
			// 规则
			const rules = {
				mobile: {
					required: true,
					tel: true
				},
				smsCode: {
					required: true
				}
			}
			// 提示
			const messages = {
				mobile: {
					required: "请输入手机号！"
				},
				smsCode: {
					required: "请输入验证码"
				}
			}
			this.WxValidate = new WxValidate(rules, messages)
		},
		// 获取验证码
		getMobileCode() {
			if (this.bindmobile) {
				if (this.mobileCode == '获取验证码') {
					this.time = 60
					this.timer()
					var jiamiMobile = this.$encruption(this.bindmobile)
					const params = {
						mobile:this.bindmobile,
						elmobi: jiamiMobile
					}
					console.log(params)
					this.$request.url_request(SENDCODE, params, 'POST', res => {
						uni.showToast({
							title: '验证码发送成功',
							icon: 'none',
							duration: 1000
						})
					}, err => {})
				}
			} else {
				uni.showToast({
					title: '手机号不可为空',
					icon: 'none',
					duration: 1000
				})
			}
		
		
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		},
		// 登录
		handleLogin(e) {
			var that = this
			this.isDisable = true
			setTimeout(function() {
				that.isDisable = false
				that.$forceUpdate()
			}, 1000)
			const params = e.detail.value
			if (!this.WxValidate.checkForm(params)) {
				const error = this.WxValidate.errorList[0]
				// 可以自定义提示
				uni.showToast({
					title: `${error.msg}`,
					icon: 'none'
				});
				return false
			} else {
				if(this.isCheck == false){
					uni.showToast({
						title:'请阅读隐私协议',
						icon:'none',
						duration:1200
					})
				} else{
					var _this = this
				this.$request.url_request(LOGIN, params, "POST", res => {
					if (JSON.parse(res.data).code == 0) {
						uni.navigateTo({
							url: '../../Welcome/Welcome'
						})
					} else {
						if (JSON.parse(res.data).msg) {
							uni.showToast({
								title: JSON.parse(res.data).msg,
								icon: 'none',
								duration: 1000
							})
						}
					}
				}, err => {
		
				});
				}
				
			}
		},
		backtohome(){
			uni.navigateTo({
				url:'../bindMobile'
			})
		},
		open() {
			this.$refs.popup.open()
		},
		checkboxChange(value) {
			this.isCheck = !this.isCheck
		}
	}
}
